import "./js/carousel1"  //
import './js/carousel2'  //
import './js/sidebar'
import './js/easing'

//
var mySwiper = new Swiper('#case1', {
    initialSlide: 0, //0
    speed: 2000, //
    // /* grabCursor: true,// */
    autoplay: {
        // /*  3000 */
        delay: 2500,
        // 
        disableOnInteraction: false
    },

    // <!--  -->
    pagination: {
        el: '.swiper-pagination',
        clickable: true
    },
    // <!--   -->
    navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev'
    },
    // <!--  -->
    scrollbar: {
        el: '.swiper-scrollbar',
        hide: true
    }

});

// 
var clientHeight = document.documentElement.clientHeight;
var pagePosition = 0

var pageResize = function() {
    $(window).resize(function() {
        clientHeight = document.documentElement.clientHeight;
    });
};

// banner
var setHeadPicHeight = function() {
    var headPicDOM = $('#headpic');
    var width = headPicDOM.width();
    console.log(width)
    var height = Math.floor(width / (2880 / 1180));
    headPicDOM.css({ height: height });
};

var animation = function() {
    // 
    $(window).on("scroll load", function() {
    // $(window).scroll(function() {
        pagePosition = $(window).scrollTop() + clientHeight;
        /*  =>  =>  => */
        if (pagePosition > 1050) {
            $('.title-3').css({
                "opacity": 1,
                "top": 186 + 'px'
            });
        } else {
            $('.title-3').css({

                "opacity": 0,
                "top": 250 + 'px'
            });
        }

        var cssShowObj = {
            "top": 50 + 'px',
            "opacity": 1,
            "transition": "all 0.2s"
        };
        var cssHideObj = {
            "top": 100 + 'px',
            "opacity": 0,
            "transition": "all 0.2s"
        };

        // (\\)
        if (pagePosition > 1250) {
            $('.text-title').css({ "opacity": 1, "top": 380 + 'px' });
            $('.iphone-part').css({ "top": 340 + 'px', "opacity": 1 });
            setTimeout(function(){
             $('.delicacy').css(cssShowObj);
            }, 0);
            setTimeout(function(){
              $('.desserts').css(cssShowObj);
            }, 300);
            setTimeout(function(){
              $('.fresh').css(cssShowObj);
            }, 600);
            setTimeout(function(){
              $('.supermarket').css(cssShowObj);
            }, 900);
        } else {
            $('.text-title').css({ "opacity": 0, "top": 450 + 'px' });
            $('.iphone-part').css({"top": 420 + 'px', "opacity": 0 });

            $('.delicacy').css(cssHideObj);
            $('.desserts').css(cssHideObj);
            $('.fresh').css(cssHideObj);
            $('.supermarket').css(cssHideObj);
        }

        // 
        if (pagePosition > 2000) {
            $('.title-4').css({
                "opacity": 1,
                "top": 90 + 'px'
            });
        } else {
            $('.title-4').css({
                "opacity": 0,
                "top": 140 + 'px'
            });
        }

        if (pagePosition > 2200) {
            $('.half-background').css({
                "opacity": 1,
                "top": 0 + 'px'
            });
            $('.arrows').css({
                "opacity": 1,
                "transition": "all 0.5s"
            })
        } else {
            $('.half-background').css({
                "opacity": 0,
                "top": 100 + 'px'
            });
        }

        if (pagePosition > 2200){
             $(".story-slider-container").css({
                "opacity": 1,
                "top": 30 + 'px'
             });

        } else {
            $(".story-slider-container").css({
                "opacity": 0,
                "top": 80 + 'px'
             });
        }



        // 
        if (pagePosition > 2900){
            $('.title-5').css({
                "opacity": 1,
                "top": 80 + 'px'
            });
        } else {
            $('.title-5').css({
                "opacity": 0,
                "top": 180 + 'px'
            });
        }

        // slider
        if (pagePosition > 3100) {
            $('.news-left').css({
                "opacity": 1,
                "top": 0 + 'px'
            });
            $('.news-title').css({
                "opacity": 1,
                "top": 0 + 'px'
            });
            setTimeout(function(){
                $('.news-background').css({
                    "opacity": 1,
                    "top": 40 + 'px',
                    "transition": "all 1.3s"
                });
               }, 100);
            setTimeout(function(){
                $('.news-right').css({
                    "opacity": 1,
                    "top": 0 + 'px'
                });
               }, 300);
            
        } else {
            $('.news-left').css({
                "opacity": 0,
                "top": 100 + 'px'
            });
            $('.news-title').css({
                "opacity": 0,
                "top": 100 + 'px'
            });
            setTimeout(function(){
                $('.news-background').css({
                    "opacity": 0,
                    "top": 100 + 'px'
                });
               }, 100);
            $('.news-right').css({
                "opacity": 0,
                "top": 100 + 'px'
            });
        }
    });

};

// 
var addEvent = function() {
    setHeadPicHeight();
    $(window).resize(function() {
        setHeadPicHeight();
    });
    animation();
    pageResize();
};

addEvent();

// 
$(".classified").find('li').mouseenter(function(){
    $(this).addClass('text-part-picked')
})
$('.classified').find('li').mouseleave(function(){
    $(this).removeClass('text-part-picked')
})





